/* CSS */
.subbutton {
  display: inline-flex;
  align-items: center;
  /* backdrop-filter: blur(.9); */
  background-image: linear-gradient(144deg,#AF40FF, #5B42F3 50%,#00DDEB);
  border: 0;
  border-radius: 8px;
  /* box-shadow: rgba(151, 65, 252, 0.2) 0 15px 30px -5px; */
  box-sizing: border-box;
  color: #FFFFFF;
  font-family: Phantomsans, sans-serif;
  font-size: 20px;
  justify-content: center;
  line-height: 1em;
  padding: 3px;
  cursor: pointer;
}

.subbutton span {
  /* background-color: rgb(5, 6, 45); */
  padding: 10px 24px;
  border-radius: 6px;
  width: 100%;
  height: 100%;
  transition: 300ms;
}




.delbutton {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  display: inline-block;
}

